import { useEffect, useState }  from "react"
import { Link, useNavigate }    from "react-router-dom"
import { useDispatch }          from 'react-redux';
import { useForm }              from "react-hook-form";

import Footer                   from "../menu/Footer";

import { login }                from 'service/UserService';
import { setRefreshToken, setUserId }  from 'storage/Cookie';
import * as auth                from 'store/Auth';

/**
 * Front - 
 */
function Login() {
    const { errMsg, setErrMsg } = useState(null);   //  
    const navigate              = useNavigate();
    const dispatch              = useDispatch();

    // useForm   
    const { register, setValue, formState: { errors }, handleSubmit, setFocus } = useForm();

    const init = {
        userId        : ""    //  
        , emailServer : ""    //     
        , rememberYn  : false //  
        , retUrl      : ""    //   URL
    }

    // submit   
    //    
    const onValid = async (data) => {
        // Step.   
        if(data.emailServer === "") data.userId = data.id;
        else                        data.userId = data.id + "@" + data.emailServer;

        const response = await login(data);
        console.log(response);
        if (response.status) {
            //  Refresh Token, store Access Token 
            const { refresh_token, access_token, userId, loginUser } = response.json;
            setRefreshToken(refresh_token);
            setUserId(userId);
            dispatch(auth.SET_TOKEN(access_token));
            dispatch(auth.SET_USER(loginUser));

            return navigate("/");
        } else {
            setErrMsg(response.json.error.message);
        }
        
        // input    
        setValue("userPwd", "");
    };
    
    // Email Server 
    const servers = ["naver.com", "daum.net", "gmail.com", "nate.com", "hanmail.com"];

    // Key Event Handler
    const hndlKeyUp = e => {
        if(e.key === 'Enter'){
            if(e.target.name === "id"){
                // 
            }else if(e.target.name === "userPwd"){
                // 
                // handleSubmit(onValid)
            }
            // console.log(e.target);
        }
    }

    //  focus 
    useEffect(() => {
        setFocus("id");
    }, [setFocus]);

    return (
        <>
            <div id="login-wrap">
                <div className="form-login">
                    <h4>&nbsp;</h4>
                    <form onSubmit={handleSubmit(onValid)}>
                        <input type="hidden" {...register("userId")}/>
                        <input type="hidden" {...register("rememberYn")}/>
                        <input type="hidden" {...register("retUrl")}/>
                        <ul>
                            <li>
                                <input {...register("id", {required:"Please Enter Your ID"})} type="text" placeholder="" className="email-addr" onKeyUp={hndlKeyUp}/>
                                <select {...register("emailServer")} className="email-server">
                                    <option value=""></option>
                                    {
                                        servers.map(server => (
                                            <option key={"opt" + server} value={server}>{server}</option>
                                        ))
                                    }
                                </select>
                            </li>
                            <li><input {...register("userPwd", {required:"Please Enter Your Password"})} type="password" placeholder="" onKeyUp={hndlKeyUp}/></li>
                            <li><Link onClick="toggleRemember(this);return false;" className={'btn_idsave active'}><em></em></Link></li>
                        </ul>
                    </form>
                    <Link className="btn_login" onClick={handleSubmit(onValid)}><img src="/images/contents/btn_login.png"/></Link>
                    <Link to="/join" className="btn_join"></Link>
                    <p className="btn_find">
                        <Link to="/find/id"> </Link>
                        <Link to="/find/pwd"> </Link>
                    </p>
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Login;