import { Component }        from "react"

/**
 * 
 */
export default class Bookmark extends Component {
    constructor(props){
        super(props);

        this.state = {
            layer:{
                search      : false     //   
              , location    : false     //  
              , bookmark    : false     //   
              , address     : false     //   
              , share       : false     //  
              , editmarker  : false     //   
            }
          , addmarker       : false     //    
          , cond: {
                openYn      : false     //    
              , selfYn      : false     //      
            }
          , location: {
                lat         : 33.450701
              , lng         : 126.570667
            }
          , level           : 3         //  
        }
    }

    render() {
        // Func:    
        const fnOpenLayer = (e) => {
            e.preventDefault();
            let target = e.target.target;
            console.log(target);
            this.setState(prevState => ({ layer:{ ...prevState.layer, [target]:true }}))
        }

        // Func:    
        const fnCloseLayer = target => {
            this.setState(prevState => ({ layer:{ ...prevState.layer, [target]:false }}))
            console.log(this.state.cond)
        }

        // Func:   
        const fnChangeState = (key, value) => {
            this.setState(prevState => ({ cond:{ ...prevState.cond, [key]:value }}))
        }

        // Func:    
        const fnTraceSuccess = (position) => {
            const lat = position.coords.latitude;
            const lng = position.coords.longitude;

            console.log(' : ' + lat + '  : ' + lng);
            this.setState({location:{lat, lng}});
        }
        const fnTraceError = error => {
            console.error(error);
        }
        const fnDoTrace = (e) => {
            e.preventDefault();

            if (navigator.geolocation) { // GPS 
                navigator.geolocation.getCurrentPosition(fnTraceSuccess, fnTraceError, {
                    enableHighAccuracy  : false
                  , maximumAge          : 0
                  , timeout             : Infinity
                });
            } else {
                alert('GPS  ');
                return;
            }
            
        }

        return (
            <>
            </>
        )
    }
}