import { BrowserRouter as Router, Routes, Route }  from 'react-router-dom';

import PrivateRoute from './routes/PrivateRoute';
import PublicRoute  from './routes/PublicRoute';


import Login    from 'pages/front/auth/Login';      // 
import Logout   from 'pages/front/auth/Logout';     // 
import Join     from 'pages/front/auth/Join';       // 
import Find     from 'pages/front/auth/Find';       // / 
import Main     from 'pages/front/main';            // 
import Bookmark from 'pages/front/map/Bookmark';    // 
import Board    from 'pages/front/board';           // 
import Friend   from 'pages/front/friend';          // 
import Mypage   from 'pages/front/mypage';          // MY PAGE

function App() {
  return (
    <div id="wrap">
      <Router>
        <Routes>
          {/* <Route path="/"       element={<Main/>}/> */}
          <Route element={<PrivateRoute />}>
            <Route path="/"         element={<Main />} />
            <Route path="/main"     element={<Main />} />
            <Route path="/bookmark" element={<Bookmark />} />
            <Route path="/board"    element={<Board />} />
            <Route path="/friend"   element={<Friend />} />
            <Route path="/mypage"   element={<Mypage />} />
            <Route path="/logout" element={<Logout />} />
          </Route>

          <Route path="/login"      element={<PublicRoute><Login/></PublicRoute>}/>
          <Route path="/join"       element={<PublicRoute><Join/></PublicRoute>}/>
          <Route path="/find/:type" element={<PublicRoute><Find/></PublicRoute>}/>
        </Routes>
      </Router>
    </div>
  );
}

export default App;
