import { Component }    from "react";

import instance         from "service/axiosInstance";
import SearchAddress    from "pages/common/SearchAddress";

import * as CommonUtils from "utils/CommonUtils";

/**
 * Front - Main -    
 */
 export default class  LocationEdit extends Component {
    /**
     * 
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { detail, fnCallback } = this.props;
        this.state = {
            detail
          , fnCallback
          , postcode    : false //    
        };
    }
        
    /**
     * @description    
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
    }

    /**
     * @description    
     */
     componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);
    }

    /**
     * 
     * @returns 
     */
    render(){
        const { detail, fnCallback, postcode } = this.state;

        // Func:  
        const hndlClose = () => {
            if(fnCallback){
                fnCallback("edit");
            }
        }
        
        // Func:  
        const hndlSelected = data => {
            // console.log(` : ${data.address}, : ${data.zonecode}`);
            const addr      = data.address
            const postNum   = data.zonecode;
            const latitude  = data.latitude
            const longitude = data.longitude;
            
            this.setState(prevState => ({ detail:{ ...prevState.detail, addr, postNum, latitude, longitude }, postcode:false}));
        }

        // Func:  
        const hndlChange = (key , value) => {
            this.setState(prevState => ({ detail:{ ...prevState.detail, [key]:value }}));
        }

        // Func:   
        const fnSave = () => {
            const { detail } = this.state;

            if(CommonUtils.isEmpty(detail.lTitle)){
                alert("    .")
            }else if(CommonUtils.isEmpty(detail.postNum) || CommonUtils.isEmpty(detail.addr)){
                alert(" .")
            }else{
                instance.post("/api/main/location/save", detail, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                .then(res => res.data)
                .then(data => {
                    if(data.errMsg !== undefined)   alert(data.errMsg);
                    else                            hndlClose();
                })
                .catch(e => { alert(e); })
            }
        }

        return (
            <div className="layer layer01" style={{display:"block"}}>
                <div className="layer-form">
                    <h5>   </h5>
                    <ul className="form_cont">
                        <li>
                            <label>
                                <strong>   </strong>
                                <input type="text" name="lTitle" value={detail.lTitle??""} placeholder="   " className="mt10" onChange={e => hndlChange(e.target.name, e.target.value)}/>
                            </label>
                        </li>
                        <li>
                            <label>
                                <strong></strong>
                                <input type="text" name="postNum"   value={detail.postNum??""}  readOnly={true}     onFocus={() => this.setState({postcode:true})}  placeholder=""/>
                                <input type="text" name="addr"      value={detail.addr??""}     readOnly={true}     onFocus={() => this.setState({postcode:true})}  placeholder="" className="mt10"/>
                                <input type="text" name="addrDtl"   value={detail.addrDtl??""}  placeholder="" className="mt10" onChange={e => hndlChange(e.target.name, e.target.value)}/>
                            </label>
                        </li>
                    </ul>
                    <div className="form_button t_c">
                        <button onClick={() => fnSave()} className="button"></button>
                    </div>
                </div>
                <button onClick={hndlClose} className="btn_close"> </button>
                {
                    postcode === true?
                    <SearchAddress
                        fnClose     = {() => { this.setState({ postcode:false })}}
                        fnCallback  = {hndlSelected}
                    />
                    :null
                }
            </div>
        )
    }
}