import React, { Component } from "react"

import instance             from "service/axiosInstance";

import LayerBookmarkForm    from "pages/front/map/layer/LayerBookmarkForm"
import LayerImageViewer     from "pages/front/map/layer/LayerImageViewer";
import LocationList         from "pages/front/map/layer/LocationList";      //  -   - 
import MarkerEdit           from "pages/front/map/layer/MarkerEdit"         //   /
import MarkerInfo           from "pages/front/map/layer/MarkerInfo"         //   
import MarkerSearch         from "pages/front/map/layer/MarkerSearch";      //  
import SearchAddress        from "pages/common/SearchAddress";              //  
import LayerShare           from "pages/front/map/layer/LayerShare";
import Menu                 from "pages/front/map/layer/Menu";

import Drawer               from "./comp/Drawer";
import CustomMap            from "./comp/CustomMap";

/**
 * 
 */
export default class Main extends Component {
    constructor(props){
        super(props);
        
        this.state = {
            layer:{
                menu        : false     // Drawer 
              , search      : false     //   
              , location    : false     //  
              , bookmark    : false     //   
              , address     : false     //   
              , share       : false     //  
              , edit        : false     //   
              , ibrowser    : false     //  
            }
          , addmarker       : false     //     [  ]
          , cond: {
                openYn      : false     //    
              , selfYn      : false     //      
            }
          , markers         : []        //  
          , curMarker       : null      //  
          , location        : null      //     
          , timer           : null
        }
        this.loginUser = React.createContext("loginUser")
        this.map = React.createRef();
    }

    /**
     * @description    
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);

        this.fnMySavedLocation();
    }

    /**
     * @description    
     */
     componentDidUpdate(prevProp, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(prevState.location !== this.state.location){
            if(JSON.stringify(this.map.current.location.get()) !== JSON.stringify(this.state.location)){
                this.map.current.location.set(this.state.location);
            }

            this.fnDelaySaveMyLocation(this.state.location);
        } else if(this.state.location == null) {
            this.fnMySavedLocation();
        }
    }

    /** 
     *     
     */
    fnMySavedLocation = () => {
        instance.get("/api/main/location/current")
        .then(res       => res.data)
        .then(data      => {
            if(data.length > 0 && this.map.current){
                const location      = data[0].location;
                const [ lat, lng ]  = location.split(",");
                this.setState({ location:{ lat, lng } });
            }
            })
        .catch(error    => {
            if(error.response)      console.error(error.response);
            else if(error.request)  console.log(error.request);
            else                    console.log('Error', error.message);
            console.log(error.config);
        })
    }

    /**
     * @description   (  )
     */
    fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 100);
        this.setState({ timer });
    }

    /**
     * @description   (  )
     */
     fnDelaySaveMyLocation = location => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSaveMyLocation(location); }, 100);
        this.setState({ timer });
    }
    
    /**
     * @description 
     */
     fnSearch = () => {
        if(this.map.current){
            const { openYn, selfYn }    = this.state.cond;
            const bounds                = this.map.current.location.bounds();
            const level                 = this.map.current.level.get();
            console.log(level);
            instance.post("/api/main/marker/list", { ...bounds, isOpenYn:openYn === true?"Y":"N", isMyMarker:selfYn === true?"Y":"N", level }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
            .then(res       => res.data)
            .then(markers   => this.setState({ markers }))
            .catch(error    => {
                if(error.response)      console.error(error.response);
                else if(error.request)  console.log(error.request);
                else                    console.log('Error', error.message);
                console.log(error.config);
    
                this.setState({ list:[] });
            })
        }
    }

    /**
     * @description   
     * @param {*} location 
     */
     fnSaveMyLocation = location => {
        instance.post("/api/main/location/current", { lastPos:`${location.lat},${location.lng}` }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
        .then(res       => { this.fnSearch() })
        .catch(error    => {
            if(error.response)      console.error(error.response);
            else if(error.request)  console.log(error.request);
            else                    console.log('Error', error.message);
            console.log(error.config);

            this.setState({ list:[] });
        })
    }

    /**
     *    / 
     * @param {*} latitude 
     * @param {*} longitude 
     */
    fnMoveToLoc = (latitude, longitude) => {
        console.log(' : ' + latitude + '  : ' + longitude);
        if(this.map.current)    this.map.current.location.set({lat:latitude, lng:longitude});
    }
    
    render() {
        const { layer, markers, curMarker, addmarker } = this.state;

        // Func:    
        const fnOpenLayer = target => this.setState(prevState => ({ layer:{ ...prevState.layer, [target]:true }}))

        // Func:    
        const fnCloseLayer = target => this.setState(prevState => ({ layer:{ ...prevState.layer, [target]:false }}))

        // Func:   
        const fnChangeCond = (key, value) => this.setState(prevState => ({ cond:{ ...prevState.cond, [key]:value }}))

        // Func:   
        const fnAddMarker = marker => {
            if(this.map.current){
                const center        = this.map.current.map.current.getCenter();
                marker.latitude     = center.Ma;
                marker.longitude    = center.La;
            }
            this.setState({ curMarker:marker, addmarker:false, layer: { ...layer, edit:true }});
        }
    
        // Func:   
        const fnDoTrace = () => {
            if (navigator.geolocation) { // GPS 
                navigator.geolocation.getCurrentPosition(
                    pos => {
                        const { latitude, longitude } = pos.coords;
                        this.fnMoveToLoc(latitude, longitude);
                    }
                  , err => console.log(err)
                  , {  enableHighAccuracy  : false
                     , maximumAge          : 0
                     , timeout             : Infinity
                    }
                );
            } else {
                alert('GPS  ');
                return;
            }
        }

        // Func:    
        const hndlMenu = type => {
            switch(type){
                case "menu"     :   // Drawer
                case "search"   :   //  
                case "address"  :   //  
                case "location" :   //  
                    fnOpenLayer(type);
                    break;
                case "main"     :   //  
                case "bookmark" :   //  
                case "friend"   :   //  
                case "mypage"   :   // MY PAGE
                case "board"    :   // 
                    window.location.href = `/${type}`;
                    break;
                case "trace"    :   //  
                    fnDoTrace();
                    break;
                case "openYn"   :   //  
                case "selfYn"   :   // MY 
                    fnChangeCond(type, this.state.cond[type] === true?false:true);
                    this.fnDelaySearch();
                    break;
                case "editmarker"   : //   ( )
                    fnAddMarker({});
                    break;
                case "levelPlus"    : //  Level +
                    if(this.map.current)    this.map.current.level.plus();
                    this.fnDelaySearch();
                    break;
                case "levelMinus"   : //  Level -
                    if(this.map.current)    this.map.current.level.minus();
                    this.fnDelaySearch();
                    break;
                default:
                    break;
            }
        }
        
        // Func:  
        const hndlMap = (type, marker) => {
            // console.log(type);
            // console.log(target);
            switch(type){
                case "onCenterChanged":     //   
                    const center    = this.map.current.map.current.getCenter();
                    const location  = { lat:center.Ma, lng:center.La };
                    this.setState({ location });

                    this.fnDelaySaveMyLocation(location);

                    break;
                case "onZoomChanged":       // Zoom Level  
                    this.fnDelaySearch();
                    break;
                case "showmarker":          // Custom:   
                    this.setState({ curMarker:marker });
                    this.fnSearch();
                    break;
                default:
                    break;
            }
        }

        // Func:    
        const hndlMarkerInfo = type => {
            switch(type){
                case "edit"     :
                case "share"    :
                    fnOpenLayer(type);
                    break;
                case "close"    :
                    this.setState({ curMarker:null });
                    break;
                default         :
                    break;
            }
        }

        // Func:   []  
        const hndlMarkerAdd = data => {
            // console.log(` : ${data.address}, : ${data.zonecode}`);
            const addr      = data.address
            const postNum   = data.zonecode;
            const latitude  = data.latitude
            const longitude = data.longitude;
            const param     = { addr, postNum, latitude, longitude };

            this.setState({ curMarker:param, location:{ lat:latitude, lng:longitude }, addmarker:true, layer: { ...layer, address:false }});
        }

        // Func:  EDIT  
        const hndlMarkerEdit = (type, marker) => {
            fnCloseLayer("edit");
        }

        // Func:  EDIT  
        const hndl = {
            search:(type, marker) => {
                console.log(marker);
                switch(type){
                    // Cond:   
                    case "close":fnCloseLayer("search");break;
                    // Cond: Show  
                    case "show" :this.setState({ curMarker:marker, layer: { ...layer, search:false }});break;
                    // Cond: Edit  
                    case "edit" :this.setState({ curMarker:marker, layer: { ...layer, search:false, edit:true }});break;
                    default:
                        break;
                }
            }
        }
        
        // console.log(markers);
        return (
            <>
                <CustomMap ref={this.map} markers={markers} curMarker={curMarker} fnCallback={hndlMap}/>
                { addmarker === true         ? <button className="btn_map a_center" onClick={() => fnAddMarker(curMarker)}><em></em>+ </button> : null }

                <Menu target="main" onClick={hndlMenu} openYn={this.state.cond.openYn} selfYn={this.state.cond.selfYn}/>

                <p className="info_site">PC www.mapmarker.co.kr</p>

                {
                layer.ibrowser === true?
                <div className="info_browser active">
                    <div>Internet Explorer     .    Edge    .</div>
                    <div className="site_button">
                        <button onClick={() => window.open("microsoft-edge:http://www.mapmarker.co.kr")}>Microsoft Edge </button>
                        {/* <a href="#" onclick="goChromeBrowser('http://www.mapmarker.co.kr');return false;">Chrome </a> */}
                        <button onClick={() => fnCloseLayer("ibrowser")}></button>
                    </div>
                </div>
                :null
                }

                <Drawer visible={layer.menu} onClose={() => { fnCloseLayer("menu");this.fnDelaySearch(); }}/>
                { layer.search === true     ? <MarkerSearch         callback={hndl.search}/> : null }
                <LayerImageViewer />
                { layer.bookmark === true   ? <LayerBookmarkForm    callback={fnCloseLayer} /> : null }
                { layer.location === true   ? <LocationList         fnCallback={fnCloseLayer} onMove={this.fnMoveToLoc} /> : null }
                { layer.address === true    ? <SearchAddress        fnClose={() => { fnCloseLayer("address") }} fnCallback={hndlMarkerAdd} /> : null }
                {/* <LayerLocationList callback={fnCloseLayer} onMove={this.fnMoveToLoc} /> */}
                { (curMarker??{}).mId       ? <MarkerInfo           mId={curMarker.mId} callback={hndlMarkerInfo}/> : null }
                { layer.share === true      ? <LayerShare           mId={curMarker.mId} callback={fnCloseLayer} /> : null }
                { layer.edit === true       ? <MarkerEdit           item={curMarker}    callback={hndlMarkerEdit} /> : null }
                {/* <MarkerEdit item={curMarker} callback={hndlMarkerEdit} /> */}
                                
                <div id="lyAlert"></div>
                {/* <!-- ============================== PHOTO_SWIPE ============================== --> */}
                {/* <!-- ============================== #PHOTO_SWIPE ============================== --> */}
            </>
        )
    }
}