import { Component } from "react";

const DEFAULT_LIST_SIZE = 10;   //   
const DEFAULT_PAGE_SIZE = 5;    //   
const DEFAULT_FIRST_PAGE = 1;   //   

/**
 *   
 */
export default class Pager extends Component {
    constructor(props){
        super(props);

        const { listSize, pageSize, page, totalCount, onClick } = props;

        this.state = {
            listSize    : listSize??DEFAULT_LIST_SIZE       //     
          , pageSize    : pageSize??DEFAULT_PAGE_SIZE       //     
          , page        : page??DEFAULT_FIRST_PAGE          //  
          , totalCount  : totalCount??0                     //   
          , onClick     : onClick                           //   
        }
    }

    /**
     * @description    
     */
     componentDidMount(){
        // console.log("############ Pager.componentDidMount ############");
    }

    /**
     * @description    
     */
    componentDidUpdate(prevProp, prevState){
        // console.log("############ Pager.componentDidUpdate ############");
        if(JSON.stringify(prevProp) !== JSON.stringify(this.props)){
            const { listSize, pageSize, page, totalCount, onClick } = this.props;

            this.setState({
                listSize    : listSize??DEFAULT_LIST_SIZE       //     
              , pageSize    : pageSize??DEFAULT_PAGE_SIZE       //     
              , page        : page??DEFAULT_FIRST_PAGE          //  
              , totalCount  : totalCount??0                     //   
              , onClick     : onClick                           //   
            });
        }
    }

    /**
     * @description 
     */
    render(){
        const { listSize, pageSize, page, totalCount, onClick } = this.state;

        const pageNumbers   = [];

        const totalPage     = Math.ceil(totalCount / listSize);
        const startPage     = parseInt(((page - 1) / pageSize)) * pageSize + 1;   //  
        let endPage         = (parseInt((page - 1) / pageSize) + 1) * pageSize; //  

        //              
        if(endPage > totalPage) endPage = totalPage;

        //    
        for(let nPage = startPage; nPage <= endPage; nPage++){
            pageNumbers.push(nPage);
        }

        //  
        const hndlPage = (nPage) => {
            if(onClick !== undefined)   onClick(nPage);
        }

        // Element 
        return (
            <div className="pagination">
                {page !== 1?<button title=" " onClick={() => hndlPage(page - 1)}>{`<`}</button>:null}
                {pageNumbers.map(nPage => <button key={`page${nPage}`} title={`${nPage} `} onClick={() => hndlPage(nPage)} className={nPage === page?"selected":""}>{nPage}</button>)}
                {page < totalPage?<button title=" " onClick={() => hndlPage(page + 1)}>{`>`}</button>:null}
            </div>
       )
    }
}