import { Component }        from "react"
import { Link }             from "react-router-dom";

import instance             from "service/axiosInstance";
import CommonUtils          from "utils/CommonUtils";

import Footer               from "pages/front/menu/Footer";

const IMAGE_SERVER = process.env.REACT_APP_IMAGE_SERVER;

/**
 * @path    Front - My Page
 * @url     /friend
 */
export default class Mypage extends Component {
    constructor(props){
        super(props);

        this.state = {
            user        : {}    //   
          , userPwd     : ""    //  
          , userPwdCnfm : ""    //   
        }
    }

    /**
     * 
     */
    fnSearch = () => {
        instance.get("/api/mypage/user")
        .then(res => res.data && res.data.length > 0?res.data[0]:{})
        .then(data => {
            this.setState({user:data});
        })
        .catch(error => {
            if(error.response === 'jwt must be provided'){
                this.fnSearch();
            }

            if(error.response){
                console.error(error.response);
            }else if(error.request){
                console.log(error.request);
            }else{
                console.log('Error', error.message);
            }
            console.log(error.config);
            // window.location.replace("/");
            // this.props.navigation.navigate("/");
        })
    }

    componentDidMount(){
        this.fnSearch();
    }

    render() {
        const loginUser = this.state.user;   //   

        // Func:   
        const setUserInfo = (key, value) => {
            this.setState(prevState => ({ user:{...prevState.user, [key]:value }}))
        }

        // Func:   
        const attachProfile = () => {
            // TODO:    url   
        }

        // Func:   
        const deleteProfile = () => {
            setUserInfo("profile", null);
        }

        // Func:    
        const attachImage = () => {
            // TODO:    url   
        }

        // Func:   
        const deleteImage = () => {
            setUserInfo("imgUrl", null);
        }

        //  
        const doSavePwd = () => {
            if(window.confirm(" ?")){
                const { userPwd, userPwdCnfm } = this.state;
                if(CommonUtils.isEmpty(userPwd)){
                    alert(" .");
                }else if(userPwd.split(" ").length > 1){
                    alert("   ");
                }else if(userPwd !== userPwdCnfm){
                    alert("    .");
                }else{
                    instance.post("/api/mypage/password", { userPwd, userPwdCnfm }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                    .then(res => res.data)
                    .then(data => {
                        if(data.errMsg !== undefined)    alert(data.errMsg);
                        else{
                            alert(data.message);
                            this.setState({userPwd:""})
                            this.setState({userPwdCnfm:""})
                        }
                    })
                    .catch(e => { alert(e); })
                }
            }
        }

        return (
            <>
                <div id="my_wrap">
                    <div id="my_header">
                        <h4>MapMarker</h4>
                        <Link to="/" className="btn_back"></Link>
                    </div>
                    <div id="my_contents">
                        <p className="my_id">{loginUser.userId}</p>
                        <div className="form-edit">
                            <ul>
                                <li id="profile" className="profile-box">
                                    <button onClick={attachProfile} className="profile" style={{backgroundImage:loginUser.profile?`url('${IMAGE_SERVER + loginUser.profile}')`:"", backgroundSize:"contain"}}> </button>
                                    {
                                        loginUser.profile
                                        ?<button onClick={deleteProfile} className="btn-del"></button>
                                        :null
                                    }
                                </li>
                                <li>
                                    <input
                                        type        = "text"
                                        name        = "userName"
                                        value       = {loginUser.userName}
                                        placeholder = " "
                                        onChange    = {e => { setUserInfo(e.target.name, e.target.value) }}
                                    />
                                </li>
                                <li>
                                    <div className="photo">
                                        <p id="imgUrl">{loginUser.imgUrl?<img alt="myPhoto" src={IMAGE_SERVER + loginUser.imgUrl} style={{maxWidth:"200px", maxHeight:"100px", marginBottom:"10px"}}/>:null}</p>
                                        {!loginUser.imgUrl?<button className="button" onClick={attachImage}> </button>:null}
                                        {loginUser.imgUrl?<button className="button type03" onClick={deleteImage}> </button>:null}
                                        <p className="mt10">&amp;&amp;     ''    .    .</p>
                                    </div>
                                </li>
                            </ul>
                            <div className="form-button">
                                <button onClick={e => { /* doSaveName() */ }} className="btn-save"></button>
                            </div>
                        </div>
                        { /*   */ }
                        <div className="form-edit">
                            <ul>
                                <li>
                                    <input
                                        type        = "password"
                                        value       = {this.state.userPwd}
                                        name        = "userPwd"
                                        placeholder = ""
                                        onChange    = {e => this.setState({userPwd:e.target.value})}
                                    />
                                </li>
                                <li>
                                    <input
                                        type        = "password"
                                        value       = {this.state.userPwdCnfm}
                                        name        = "userPwdCnfm"
                                        placeholder = " "
                                        onChange    = {e => this.setState({userPwdCnfm:e.target.value})}
                                    />
                                </li>
                            </ul>
                            <div className="form-button">
                                <button onClick={doSavePwd} className="btn-save"></button>
                            </div>
                        </div>
                        { /*   */ }
                        { /*   */ }
                        {
                            loginUser.primeExpiredYn !== "Y"?
                            <div className="prime-card">
                                <p><strong></strong></p>
                                <p><span>{loginUser.primeDueMonth}  ({loginUser.primeStartDt} ~ {loginUser.primeExpiredDt})</span></p>
                            </div>
                            :null
                        }
                        { /*   */ }
                        <Link to="/logout" className="btn-logout"></Link>
                    </div>
                </div>
                <Footer/>
            </>
        )
    }
}