import React, { Component } from "react"
import { Link }             from "react-router-dom";

import BoardDetail          from "./comp/BoardDetail";
import BoardList            from "./comp/BoardList";

/**
 * @path    Front - 
 * @url     /board
 */
export default class Board extends Component {
    constructor(props){
        super(props);

        this.state = {
            loginUser   : {}    //  
          , viewType    : ""    //  
          , cmntSeq     : ""    // 
          , cmntCont    : ""    // 
          , bbsSeq      : null  //   
          , page        : 1     //  
          , comments    : []    //  
        }

        this.boardList = React.createRef();
    }

    /**
     * @description    
     */
    componentDidMount(){
        console.log("############ Board.componentDidMount ############");
    }
    
    render() {
        // Func:  
        const fnGetBbsDetail = (viewType, bbsSeq) => {
            this.setState({ viewType, bbsSeq });
        }

        return (
            <div id="board_wrap">
                <div id="bbs_header">
                    <h4>MapMarker</h4>
                    <Link to="/main"    className="btn_home"></Link>
                    <button className="btn_back" onClick={() => { fnGetBbsDetail("V", "" )}}></button>
                </div>
                <div id="bbs_contents">
                    <h5 className="content-title"><strong></strong></h5>
                    <BoardDetail viewType={this.state.viewType} bbsSeq={this.state.bbsSeq} onClose={() => { fnGetBbsDetail("V", null); }}/>
                    <BoardList ref={this.boardList} bbsSeq={this.state.bbsSeq} onClick={fnGetBbsDetail} />
                </div>
            </div>
        )
    }
}