/**
 * Front - Main -  
 */
function LayerShare(props) {
    // Func:  
    const closeLayer = e => {
        e.preventDefault();
        props.callback("share");
    }

    {/* :     onClick={closeLayer} */}
    return (
        <div id="lyShare" className="layer layer01" style={{ display:"flex"}}>
            <div onclick="stopPropagation()" className="layer-share">
                <h5><a href="#" onclick="hideLayout('#lyShare');return false;" className="btn_close"> </a></h5>
                <div className="share-list">
                    <a href="#" className="kakao kakao-link" onclick="_shareKakao();return false;"></a>
                    <a href="#" className="sms" onclick="_shareSMS();return false;"></a>
                </div>
            </div>
        </div>
    )
}

export default LayerShare;