import { Component }        from "react";
import { connect}           from "react-redux";

import instance             from "service/axiosInstance";

import * as CommonUtils     from "utils/CommonUtils";

const codHouseType  = { "C0401": "h01", "C0402": "h02", "C0403": "h03", "C0404": "h04", "C0405": "h05", "C0406": "h06", "C0407": "h07", "C0408": "h08", "C0409": "h09", "C0410": "h10" };    // 

/**
 * Front - Main -   
 */
class MarkerInfo extends Component {
    /**
     * @description 
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { mId, loginUser, callback }  = props;

        this.state = {
            mId                 //  
          , loginUser           //  
          , callback            //  
          , item        : {}    //  
        };
    }

    /**
     * @description    
     */
     componentDidMount(){
        console.log(`############ ${this.constructor.name}.componentDidMount ############`);
        this.fnDelaySearch();
    }

    /**
     * @description    
     */
    componentDidUpdate(prevProps, prevState){
        console.log(`############ ${this.constructor.name}.componentDidUpdate ############`);

        if(prevProps.mId !== this.props.mId){
            this.fnDelaySearch();
        }
    }

    /**
     * @description   (  )
     */
     fnDelaySearch = () => {
        if(this.state.timer !== null){
            clearTimeout(this.state.timer);
        }

        const timer = setTimeout(() => { this.fnSearch(); }, 50);
        this.setState({ timer });
    }
    
    /**
     *  
     */
    fnSearch = () => {
        const { mId } = this.state;
        if(mId){
            instance.get("/api/main/marker/detail?mId=" + mId)
            .then(res       => res.data && res.data.length > 0?res.data[0]:{})
            .then(item      => this.setState({ item }))
            .catch(e        => {
                console.log(e);
                if(this.state.callback){
                    this.state.callback("close");
                }
            });
        }else{
            if(this.state.callback){
                this.state.callback("close");
            }
        }
    }

    /**
     * @descriptin 
     * @returns 
     */
    render(){
        const { item, loginUser, callback } = this.state;
        
        // step.  
        let houseClsName = null;
        for (let houseTypeCode in codHouseType) {
            if(item.houseType === houseTypeCode){
                houseClsName = codHouseType[houseTypeCode];
                break;
            }
        }
        
        // Step.  9
        const cntrtList = [];
        if (item.cntrtCode !== null && item.cntrtCode !== '') {
            if (!CommonUtils.isEmpty(item.cntrtCode)) {
                const codes = item.cntrtCode.split(",");
                if(codes.indexOf("C0101") >= 0)  cntrtList.push(<span key={`cntrt${item.mId}_C0101`} className="if_cntrt m01"></span>)
                if(codes.indexOf("C0102") >= 0)  cntrtList.push(<span key={`cntrt${item.mId}_C0102`} className="if_cntrt m02"></span>)
                if(codes.indexOf("C0104") >= 0)  cntrtList.push(<span key={`cntrt${item.mId}_C0103`} className="if_cntrt m04"></span>)
                if(codes.indexOf("C0103") >= 0)  cntrtList.push(<span key={`cntrt${item.mId}_C0104`} className="if_cntrt m03"></span>)
            }
        }

        // Step.  
        const priceInfo = [];
        priceInfo.push(CommonUtils.fmtCurrency(item.price, 4));
        //     ,   
        const priceMonthly = item.priceMonthly;
        if (priceMonthly !== '' && priceMonthly > 0) {
            priceInfo.push(CommonUtils.fmtCurrency(priceMonthly, 4));
        }

        // Step. 01 
        const desc01 = [];
        if (item.cntRoom > 0)   desc01.join(`${item.cntRoom}`);
        if (item.cntToilet > 0) desc01.join(`${item.cntToilet}`);
        // 
        const facilities = item.facilities;
        if (CommonUtils.isEmpty(facilities) === false) {
            const facilitieList = facilities.split(",");
            for (let idx = 0; idx < facilitieList.length; idx++) {
                const facility = facilitieList[idx];

                switch (facility) {
                    case "C0601": desc01.push(""); break;
                    case "C0602": desc01.push(""); break;
                    default:break;
                }
            }
        }
        // 
        if (item.areaPrivate > 0)   desc01.push(`${item.areaPrivate}`);
        // 
        if (item.areaSupply > 0)    desc01.push(`${item.areaSupply}`);
        // 
        if (!CommonUtils.isEmpty(item.loanTypeName))    desc01.push(item.loanTypeName);

        // Step. 
        const ifNum = [];
        ifNum.push(` #${item.mId} (: `);
        if(item.createUserName) ifNum.push(<img alt={item.createUserName} src={`http://taejin05.cafe24.com/upload/images/${item.createUserProfile}`} className="profile"/>);
        ifNum.push(`${item.createUserName})`);

        /*
        // STEP.  
        var ifPhoto = document.getElementById("ifPhoto");
        _removeChild(ifPhoto);

        var hndlViewer = function(data) { return function(e) { e.preventDefault(); _image.showImageViewer(data); }; };
        for (var i = 0; i < _image.maxCount; i++) {
            var imageKey = "imageUrl" + _lpad((i + 1), 2, '0');
            if (item[imageKey]) {
                var thumb = _createImage(_image.getImageUrl(item[imageKey]), "photo", _image.defImage, { "onerror": "this.src='" + _image.defImage + "';" });
                _addEvent(thumb, "click", hndlViewer(item));
                ifPhoto.appendChild(thumb);
            }
        }

        // STEP.   
        if (loginUser.userGbn == 'A' || item.createUserId == loginUser.userId) {
            $("#btnEdit").show();
            $("#btnReqDel").hide();
            $("#btnCancDel").hide();
        } else {
            $("#btnEdit").hide();
            $("#btnReqDel").hide();
            $("#btnCancDel").hide();
            if (!_isEmpty(item.reqDelUserId)) {
                if (data.reqDelUserId == loginUser.userId) $("#btnCancDel").show();
                else $("#btnReqDel").addClass("already").show();
            } else {
                $("#btnReqDel").removeClass("already").show();
            }
        }
        */

        let editButton      = null;           //  
        if(loginUser.userGbn === 'A' || item.createUserId === loginUser.userId){
            editButton = <button onClick={() => hndler.edit()} className="btn_edit"></button>;
        }else{
            if(!CommonUtils.isEmpty(item.reqDelUserId)){
                if (item.reqDelUserId === loginUser.userId){
                    editButton = <button onclick="doCancDel();return false;" className="btn_canc_del" id="btnCancDel"><br/></button>;
                }else{
                    editButton = <button onclick="doReqDel();return false;" className="btn_req_del already" id="btnReqDel"><br/></button>;
                }
            }else{
                editButton = <button onclick="doReqDel();return false;" className="btn_req_del" id="btnReqDel"><br/></button>;
            }
        }

        //  
        const hndler = {
            // Func:  
            close       : () => { if(callback) callback("close"); }
            // Func: 1  
          , tel01       : () => {
                if(CommonUtils.isEmpty(item.phoneNumber) === false){
                    window.location.href = `tel://${item.phoneNumber}`;
                }
            }
            // Func: 2  
          , tel02       : () => {
                if(CommonUtils.isEmpty(item.phoneNumber02) === false){
                    window.location.href = `tel://${item.phoneNumber02}`;
                }
            }
            // Func: 
          , export      : () => {
                if(CommonUtils.isMobileYn() === true)   window.location.href = `daummaps://look?p=${item.latitude},${item.longitude}`;
                else                                    window.open(`https://map.kakao.com/?map_type=TYPE_MAP&q=${item.addr}&userLevel=3`, "_blank");
            }
            // Func:  
          , bookmark    : () => {
                const { mId } = this.state.mId;
                if (item.bookmarkYn === 'Y') {
                    if (window.confirm(" ?")) {
                        // _marker.deleteBookmarkMarker();
                        instance.post("/api/main/marker/bookmark", { mId }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                        .then(res   => res.data)
                        .then(data  => {
                            if(data.errMsg !== undefined)               alert(data.errMsg);
                            else {
                                alert(".")
        
                                if(this.state.callback){
                                    this.state.callback("close");
                                }
                            }
                        })
                        .catch(e => { alert(e); })
                    }
                } else {
                    // _marker.showBookmarkMarkerForm();
                }
            }
            // Func: 
          , edit    : () => { if(callback) callback("edit"); }
            // Func: 
          , share   : () => { if(callback) callback("share"); }
        }

        return (
            <div className="layer layer02 white" style={{ display:"flex" }}>
                <div className="layer-marker">
                    <ul className="if_cont02">
                        <li className="if_addr">{item.addr}</li>
                        <li>
                            <span className={`if_addr_dtl ${houseClsName??""}`}>{item.addrDtl}</span>
                            {item.newYn === 'Y'?<span className="if_new_yn"></span>:null}
                        </li>
                        <li>
                            {cntrtList}
                            <span className={`if_price ${houseClsName??""}`}>{priceInfo.join(" / ")}</span>
                        </li>
                        <li className="if_desc01">{desc01.join(" ")}</li>
                        <li className="if_tel">{ifNum}</li>
                        {item.phoneNumber   ?<li className="if_tel">{`Tel) ${item.phoneNumber}`}</li>:null}
                        {item.phoneNumber02 ?<li className="if_tel">{`Tel) ${item.phoneNumber02}`}</li>:null}
                        <li className="if_desc02">{item.mDesc}</li>
                        <li id="ifPhoto" className="if_photo"></li>
                        <li className="if_modDt">{`: ${item.modifyDtmTxt}`}</li>
                    </ul>
                    <div className="if_button02">
                        <button onClick={() => { hndler.close(); }} className="btn_close02"></button>
                        <div className="if_tel">
                            <button onClick={() => { hndler.tel01(); }} className={`btn_tel ${item.phoneNumber  ?"":"disabled"}`}><em>1</em></button>
                            <button onClick={() => { hndler.tel02(); }} className={`btn_tel ${item.phoneNumber02?"":"disabled"}`}><em>2</em></button>
                        </div>
                        <button onClick={() => { hndler.export() }}     className="btn_exp"></button>
                        <button onClick={() => { hndler.bookmark() }}   className={`btn_bookmark ${item.bookmarkYn === "Y"?"active":""}`}></button>
                        {editButton}
                        <button onClick={() => { hndler.share() }}      className="btn_share"></button>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    loginUser: state.authToken.loginUser,
});

const mapDispatchToProps = (dispatch) => ({
    // SET_USER: user => dispatch(user)
});

export default connect(mapStateToProps, mapDispatchToProps)(MarkerInfo);