import { Component }    from "react";

import instance         from "service/axiosInstance";
import * as CommonUtils from "utils/CommonUtils";
import Pager            from "pages/common/Pager";

/**
 * Front -  -   - 
 */
export default class BoardComment extends Component {
    /**
     * 
     * @param {*} props 
     */
    constructor(props){
        super(props);

        const { bbsSeq } = props;
        this.state = { 
            bbsSeq
          , page        : 1     //  
          , comments    : []    //  
          , totalCount  : 0     //   
          , cmntSeq     : ""    //   
          , cmntCont    : ""    //   
        };
    }

    /**
     * @description    
     */
     componentDidMount(){
        console.log("############ BoardComment.componentDidMount ############");

        const { bbsSeq, page } = this.state;
        this.fnSearch(bbsSeq, page);
    }

    /**
     * @description    
     */
    componentDidUpdate(prevProp, prevState){
        console.log("############ BoardComment.componentDidUpdate ############");

        //     
        if(prevProp.bbsSeq !== this.props.bbsSeq){
            this.setState({ cmntSeq:"", cmntCont:"", page:1 });
            this.fnSearch(this.props.bbsSeq, 1);
        }

        // Step.   
        window.scrollTo(0, 0);
    }

    /**
     * @description 
     */
    fnSearch = (bbsSeq, page) => {
        instance.get(`/api/board/comments?bbsSeq=${bbsSeq}&page=${page}`)
        .then(res       => res.data)
        .then(data      => this.setState({ comments:data[0], totalCount:data[1][0].totalCount, page:page, cmntSeq:"", cmntCont:"" }))
        .catch(error    => {
            if(error.response)      console.error(error.response);
            else if(error.request)  console.log(error.request);
            else                    console.log('Error', error.message);
            console.log(error.config);
        })
    }

    /**
     * 
     * @returns 
     */
    render(){
        const { bbsSeq, comments, cmntSeq, cmntCont, page, totalCount }  = this.state;

        // Func:  
        const doCommentSave = () => {
            if(CommonUtils.isEmpty(cmntCont)){
                alert(" .");
            }else{
                instance.post("/api/board/addComment", { bbsSeq, cmntSeq, cmntCont }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                .then(res => res.data)
                .then(data => {
                    if(data.errMsg !== undefined)    alert(data.errMsg);
                    else{
                        this.fnSearch(bbsSeq, 1);
                    }
                })
                .catch(e => { alert(e); })
            }
        }

        // Func:  
        const doCommentDelete = (cmntSeq) => {
            if(window.confirm("?")){
                instance.post("/api/board/delComment", { cmntSeq }, {headers: { 'Content-Type': 'application/json;charset=UTF-8' }})
                .then(res => res.data)
                .then(data => {
                    if(data.errMsg !== undefined)    alert(data.errMsg);
                    else{
                        this.fnSearch(bbsSeq, 1);
                    }
                })
                .catch(e => { alert(e); })
            }
        }

        //  
        const hndlPage = (nPage) => {
            this.fnSearch(bbsSeq, nPage);
        }

        return(
            <div className="bbs-comment">
                <div className="form-comment">
                    <textarea name="cmntCont" value={cmntCont} onChange={e => this.setState({[e.target.name]:e.target.value})}/>
                    <button onClick={doCommentSave}></button>
                </div>
                <div className="list-comment">
                    <ul>
                        {comments.map((item, index) => (
                            <li key={`cmnt${index}`}>
                                <p className="cmnt-info">
                                    <strong>{item.createUserName}</strong>
                                    <span>{item.createDtmStr}</span>
                                    {item.selfYn === 'Y'?<button className="btn-del" title=" " onClick={e => { doCommentDelete(item.cmntSeq); }}></button>:null}
                                </p>
                                <div className="cmnt-cont">{item.cmntCont}</div>
                            </li>
                        ))}
                    </ul>
                    <Pager listSize={5} page={page} totalCount={totalCount} onClick={hndlPage}/>
                </div>
            </div>
        );
    }
}